#projects{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#projectsDekho{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    border-top:1px solid black ;
}
.projectsPageTitle{
    font-size: 2.5rem;
    font-weight: 600;
    margin-top: 1rem;
    margin-bottom: 1.5rem;
}
.projectsDesc{
    font-weight: 600;
    font-size: 1.2rem;
    max-width: 51rem;
    padding: 0 2rem;

}
.projectsBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.projectsBar{
    display: flex;
    margin: 1rem;
    padding: 0.5rem 0.5rem;
    border-radius: 0.5rem;
    background: rgba(255, 255, 255, 0.175);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}
.projectsBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 2rem;
}
.projectsBarText{
    font-size: 1rem;
    font-weight: 800;
}
