#intro{
    height: calc(92vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.332);
    border-radius: 3px;
    background: rgba(255, 255, 255, 0.175);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
    
}
.bg{
    position: absolute;
    top: -1rem;
    height: 650px;
    right: 0;
    z-index: -1;
    object-fit: cover;
   
}
.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 300;
}
.intrName{
    color: yellow;
}
.introPara{
    font-size: 20px;
    font-weight: 600;

}


@media screen and (max-width:840px){
    .bg{
        right: -25px;
        
    }
    .introContent{
        font-size: 10vw;
    }
    section#intro{
        height: 42rem;
    }
}
@media screen and (max-width:560px){
    .introContent{
        font-size: 10vw;
    }
}

@media screen and (max-width:1430px){
    #intro{
        height: 37rem;
    }
    .bg{
        margin-right: 3rem;
    }
}
