#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    border-top:1px solid black ;
    
}
.skillsTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.skillDesc{
    font-weight: 600;
    font-size: 1.2rem;
    max-width: 51rem;
    padding: 0 2rem;

}
.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 0.5rem 0.5rem;
    border-radius: 0.5rem;
    background: rgba(255, 255, 255, 0.175);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}
.skillBarImg{
    object-fit: cover;
    height: 5rem;
    width: 5rem;
    margin: 2rem;
}
.skillBarText{
    font-size: 1.2rem;
    font-weight: 800;
}

@media  screen and (max-width:720px) {
    .skillBarText>p{
        font-size: 2vw;
    }
    
}

@media  screen and (max-width:480px) {
    .skillBarText>p{
        font-size: 3vw;
    }
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }
    .skillBarText>h2{
        font-size: 5vw;
    }

    
}
